body {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  font-family: "Roboto", sans-serif;
}

.App {
  text-align: center;
  /* margin-top: 40%; */
}

img {
  height: 4rem;
  width: 4rem;
}

.frog-cursor {
  cursor: url('./assets/cursor.cur'), auto;
}

#cursor-trail {
  position: fixed;
  top: 0;
  left: 0;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: red;
  /* Change the color as desired */
  pointer-events: none;
  /* Allows the mouse to pass through the element */
}

.nice-button {
  background-color: #4CAF50;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  border-radius: 5px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.3);
  transition: all 0.3s ease;
  cursor: pointer;
}

.nice-button:hover {
  background-color: #3e8e41;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.5);
}